.meet{
    display: grid;
    grid-template-columns: 55% 45%;
    overflow: hidden;
   
}

.meet-content{
    padding: 17px;
    background-color: rgb(0, 44, 68);
}

.meet-content div{
    margin: 50px 0px;
    color: white;
}

.meet-content div h2{
    text-align: center;
    color: beige;

}

.meet-content div p{
    margin: 20px 0px;
    text-align: justify;
    line-height: 1.5em;
}

.meet-video{
    position: relative;
}
.meet-video::before{
    position: absolute;
    content: '';
    top:0;
    left:0;
    width: 10% ;
    opacity: 0.2;
    height: 100%;
   
 
    background-color: aqua;
}


@media screen and (max-width:1135px) {
    .meet{
        display: block;
    }
    .meet-content{

        font-size: 15px;
        opacity: 0.85;
        height: 100%;
        width: 100%;
        background-color: none !important;
    }

    .meet-video{
        position: absolute;
        top:15%;
        left: 50%;
        z-index: -2;
        width: fit-content;
        height: fit-content;
    }

    .meet-video video{
        width: 100%;
        height: 100%;
    }
}