.main-sec{
    display: grid;
    grid-template-columns: 65% 35%;
    align-items: center;
    justify-items: center;

    z-index: 0;
}

.main-form{

    align-self: center;
    justify-self: center;
   background-color:  rgba(245,245,245,255);
}

.image-half img{
    width: 100%;
    z-index: -1;
    
    height: 100%;
}

.image-half{
    position: relative;
    width: 100%;
    height: 100%;
}

.image-half::before{
    position: absolute;
    content: '';
    top:0px;
    height: 100%;
    left:0;
    z-index: 2;
    width: 100%;
    /* opacity: 0.2; */
    /* background-color: blue; */
}


.main-calender{
    position: absolute;
    top: 20%;
   right: 5.7%;
    z-index: 9;
    display: none;
}

.main-calender button img{
    max-width: 30px;
    max-height: 30px;
    position: absolute;
    right: 0;
    top:0;
}

@media screen and (max-width:1130px) {
    
    .main-sec{
        display: block;    
        background-color: rgb(36, 36, 57);
    }

    .main-form{
        width: 70%;
        margin: 0 auto;
        background-color: rgb(36, 36, 57);
    }

    .image-half{
        display: none !important;
    }

    .main-calender{
        width: fit-content;
        left: 170px;
    }

    .main-calender button img{
        right: 0px;
        top:8px;
    }
}

@media screen and (max-width:768px) {
    .main-calender{
        width: fit-content;
        bottom: 10px;
    }
   
    .main-form{
        width:85%;
        margin: 0 auto;
        background-color: rgb(36, 36, 57);
    }
 }