.form{
    background-color: rgb(210, 210, 246);
    padding: 24px;
    border-radius: 12px;
    width: 620px;
    margin: 20px 0px;
    height: 630px;
    
}

.form-1{
    background-color: white;
    padding: 24px;
    border-radius: 12px;
    -webkit-box-shadow: 1px 1px 5px 0px rgba(0,0,0,0.75);
-moz-box-shadow: 1px 1px 5px 0px rgba(0,0,0,0.75);
box-shadow: 1px 1px 5px 0px rgba(0,0,0,0.75);
    position: relative;
    height: 580px;
}

.form-1 .c1{
   
    -webkit-box-shadow: 1px 1px 5px 0px rgba(0,0,0,0.75);
-moz-box-shadow: 1px 1px 5px 0px rgba(0,0,0,0.75);
box-shadow: 1px 1px 5px 0px rgba(0,0,0,0.75);
    background: rgb(0,173,233);
background: -moz-linear-gradient(22deg, rgba(0,173,233,1) 0%, rgba(141,170,255,1) 0%, rgba(0,59,153,1) 100%);
background: -webkit-linear-gradient(22deg, rgba(0,173,233,1) 0%, rgba(141,170,255,1) 0%, rgba(0,59,153,1) 100%);
background: linear-gradient(22deg, rgba(0,173,233,1) 0%, rgba(141,170,255,1) 0%, rgba(0,59,153,1) 100%);
filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#00ade9",endColorstr="#003b99",GradientType=1);
padding: 16px;
border-radius: 14px;

}

.form-1 .c1 input{
    width: 70%;
    height: 28px;
    padding: 8px;
    font-size: 17px;
    letter-spacing: 1px;
    color:rgb(97, 30, 205);
  
    border: 1px solid white;
    border-radius: 4px;
    background-color: none;
    
}

.form-1 .c1 p{
    margin-top: 10px;
    margin-bottom: 2px;
    color:white;
    font-size: 17px;
}

.form-1 .c2{
    margin: 30px 0px;
    position: absolute;
    width: 500px;
    -webkit-box-shadow: 1px 1px 5px 0px rgba(0,0,0,0.75);
    -moz-box-shadow: 1px 1px 5px 0px rgba(0,0,0,0.75);
    box-shadow: 1px 1px 5px 0px rgba(0,0,0,0.75);

    background: rgb(0,173,233);
    background: -moz-linear-gradient(22deg, rgba(0,173,233,1) 0%, rgba(247,158,54,1) 0%, rgba(187,34,0,1) 100%);
    background: -webkit-linear-gradient(22deg, rgba(0,173,233,1) 0%, rgba(247,158,54,1) 0%, rgba(187,34,0,1) 100%);
    background: linear-gradient(22deg, rgba(0,173,233,1) 0%, rgba(247,158,54,1) 0%, rgba(187,34,0,1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#00ade9",endColorstr="#bb2200",GradientType=1);



padding: 16px;
border-radius: 14px;
left: -80px;
top:30%;
}

.form-1 .c2 input{
    width: 70%;
    height: 28px;
    padding: 8px;
    font-size: 17px;
    letter-spacing: 1px;
    color:rgb(45, 43, 49);
  
    border: 1px solid white;
    border-radius: 4px;
    background-color: none;
    
}

.form-1 .c2 textarea{
    outline: none;
    border: none;
    padding: 10px;
    font-size: 17px;
    
}

.form-1 .c2 p{
    margin-top: 10px;
    margin-bottom: 2px;
    color:white;
    font-size: 17px;
}

.form-1 .c3{
    margin: 30px 0px;
    position: absolute;
    width: 500px;
    -webkit-box-shadow: 1px 1px 5px 0px rgba(0,0,0,0.75);
    -moz-box-shadow: 1px 1px 5px 0px rgba(0,0,0,0.75);
    box-shadow: 1px 1px 5px 0px rgba(0,0,0,0.75);


    background: rgb(23,131,121);
    background: -moz-linear-gradient(22deg, rgba(23,131,121,1) 0%, rgba(138,157,255,1) 82%);
    background: -webkit-linear-gradient(22deg, rgba(23,131,121,1) 0%, rgba(138,157,255,1) 82%);
    background: linear-gradient(22deg, rgba(23,131,121,1) 0%, rgba(138,157,255,1) 82%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#178379",endColorstr="#8a9dff",GradientType=1);

padding: 16px;
border-radius: 14px;
right:  -80px;
top:67%;
}

.form-1 .c3 .checkbox{
    display: flex;
    align-items: center;
    text-align: center;
    gap:22px;
}

.form-1 .c3 input{
    cursor: pointer;
    font-size: 17px;
    color:rgb(97, 30, 205);
    border-radius: 4px;

    
}

.form-1 .c3 p{
    margin-top: 10px;
    margin-bottom: 2px;
    color:white;
    font-size: 17px;
}
.form-1 .c3 img{
    max-width: 40px;
    max-height: 40px;
}

.form-1 .c3-btns button{
background-color: rgb(0, 98, 190);
    padding: 12px 18px;
    font-size: 15px;
    color:rgb(255, 255, 255);
    border-radius: 6px;
    border: none;
cursor: pointer;
    
    margin: 20px;
}

.form-1 .c3 .c3-btns{
    display: flex;
    justify-content: center;
    align-items: center;
    gap:20px;
}

.form-1 .slot{
    position: absolute;
    bottom:40%;
    right: -60px;
    background-color: rgb(0, 112, 217);
    padding: 12px 18px;
    font-size: 15px;
    color:rgb(255, 255, 255);
    border-radius: 6px;
    border: none;
cursor: pointer;
    
    margin: 20px;
}


@media screen and (max-width:1130px) {
    .form{
     background-color: rgb(210, 210, 246);
 
    }
 }


 @media screen and (max-width:768px) {

    .form{
     background-color: rgb(210, 210, 246);
 
    }

    .form-1 .c2{
        width: 100%;
        position:static;
    }

    .form-1 .c3{
        width: 100%;
        position:static;
    }

    .form-1 .slot{
        position: static;
        /* display: flex; */
        text-align: center;
    }
 }