.navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 4px 20px;
    background-color: white;
}

.navbar .logo img{
    max-width: 56px;
    max-height: 56px;
}


.navbar div{
    display: flex;
    gap:20px;
}

.navbar img{
    max-width: 35px;
    max-height: 35px;
}

.ham-links{
    display: flex;
}

.ham-icon{
    display: none !important;
}

@media screen and (max-width:1130px) {
   .navbar{
    background-color: rgb(36, 36, 57);

   }
}

@media screen and (max-width:768px) {
    .navbar{
     background-color: rgb(36, 36, 57);
 
    }

    .ham-links{
    
        display: none !important;
    }

    .ham-icon{
        display: inline !important;
    }
 }